					}
				}
			}
		}
	}
	&_phone {
		width: 100%;
		margin-top: 13px;
		> a {
			display: flex;
			align-items: center;
			justify-content: end;
			font-family: 'Oswald';
			font-style: normal;
			font-weight: normal;
			font-size: 16px;
			line-height: 24px;
			color: white !important;
