}
.publications-items {
	justify-content: space-evenly;
}
.menu_phone-visible {
	margin-left: 25px;
	> a {
		font-family: 'Oswald';
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 24px;
	color: white !important;
	&:hover {
