}

.header {
	padding: 30px 0px 19px 0px;
	background: url(../img/header-background.png) no-repeat top;
	&_content {
		display: flex;
		flex-direction: column;
		justify-content: start;
		height: 96vh;
		&__bottom {
			padding-top: 26%; 
			&-btn {
				padding: 8px 55px;
			}
		}
	}
	&__main {
		background: none !important;
		padding-bottom: 50px;
		& .dropdown-mobile-menu__list #triangle-down {
			border-top: 6px solid white;
		}
		& .menu {
			align-items: center;
		}
		& #triangle-down {
			border-top: 6px solid black;
		}
		& .dropdown-mobile-menu a {
			color: white !important;
		}
		& a {
			color: black !important;
		}
		& .logo {
			flex: 0 0 15%;
		}
		& .header_content__bottom {
			display: none;
		}
		& .header_content {
			height: auto;
		}
	}
}

.footer {
	background-color: #1D1D1D;
	border-bottom: 1px solid #D72626;;
	&_wrapper {
		padding: 30px 0px 5px 0px;
	}
	&_menu {
		> ul {
			display: flex;
			justify-content: space-between;
			> li {
				> a {
					color: white;
					&:hover {
						color: white;
					}
				}
			}
		}
	}
	&_bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-top: 53px;
		padding-bottom: 14px;
		&__info {
			> img {
				margin-right: 8px;
			}
		}
		&__links {
			> a {
				text-decoration: none;
			}
			> a:not(:last-child) {
				margin-right: 30px;
			}
		}
		&__contacts {
			> a {
				text-decoration: none;
				color: white;
			}
		}
	}
}

.members {
	&_slider {
		&__dots	{
			text-align: center;
			> div {
