				height: 10.75px;
				background:#2F2F2F;
				cursor:pointer;
				border-radius:16px;
				margin:0 1px;
			}
		}
	}
}

.news {
	&_items {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		&__item {
			cursor: pointer;
			display: flex;
			flex-basis: 49%;
			padding-left: 0px;
			padding-right: 0px;
			border-radius: 5px;
			filter: drop-shadow(0px 0px 15px rgba(0, 0, 0, 0.15));
			background-color: #ffffff;
			margin-bottom: 30px;
			&-left {
				position: relative;
				&-btn {
					position: absolute;
					left: 9%;
					top: 80%;
					padding: 7.5px 20.5px;
				}
			}
			&-right {
				display: flex;
				flex-direction: column;
				padding: 23px 15px 0px 16px;
			}
		}
	}
}

.projects {
	&_items {
		height: 890px;
		&__item {
			position: relative;
			min-height: 850px;
			&-top {
				position: absolute;
				right: 30px;
				top: 30px;
				text-align: right;
				> a {
					padding: 8px 19px;
				}
			}
			&-bottom {
				position: absolute;
				left: 30px;
				bottom: 30px;
				text-align: left;
				> a {
					padding: 12px 19px;
					@media (max-width: 768px) {
