					}
					@media (max-width: 768px) {
						font-size: 14px;
					}
				
					@media (max-width: 500px) {
						font-size: 9px;
					}
				}
			}
		}
	}
	&_dots	{
		margin-top: 20px;
		text-align: center;
		> div {
			display:inline-block;
			width: 10px;
			height: 10.75px;
			background:#2F2F2F;
			cursor:pointer;
			border-radius:16px;
			margin:0 1px;
		}
	}
}

