#nav-icon1 {
    width: 30px;
    height: 14px;
    position: relative;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
}
#nav-icon1 span {
    display: block;
    position: absolute;
    height: 2px;
    width: 70%;
    background: #FFFFFF;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
}
#nav-icon1 span:nth-child(1) {
    top: 0px;
}
#nav-icon1 span:nth-child(2) {
    top: 6px;
}
#nav-icon1 span:nth-child(3) {
    top: 12px;
}
#nav-icon1.open span:nth-child(1) {
    top: 13px;
    transform: rotate(135deg);
}
#nav-icon1.open span:nth-child(2) {
    opacity: 0;
    left: -60px;
}
#nav-icon1.open span:nth-child(3) {
    top: 13px;
    transform: rotate(-135deg);
}

.dropdown-mobile-menu {
	display: none;
	position: absolute;
    top: 50px;
    height: 100%;
    width: 100%;
    background-color: #1D1D1D;
    left: 0;
    z-index: 99;
	animation: ani 0.5s forwards;
	& a {
		color: white;
	}
	&__list {
		margin-top: 30px;
		padding: 0px;
		text-align: center;
		list-style: none;
		& > li {
			margin-bottom: 20px;
		}
	}
	&__phone {
		text-align: center;
		margin-top: calc(100vh - 535px);
		& > a {
			font-family: 'Oswald';
			font-style: normal;
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			color: #fff!important;
		}
	}
}

.active-menu {
	display: block !important;
}

@keyframes ani {
	0% {transform: translateX(-150%);}
	100% {transform: translateY(0);}
}

#triangle-down {
